.my-portfolio {
  .tile {
    .container {
      position: relative;
      text-align: center;
      color: white;
    }
    .centered {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      a {
        color: white;
        text-decoration: none;
        font-size: 20px;
        &:hover {
          color: #f1f1f1;
        }
      }
    }
  }
}
